div.antragsliste2 {
  overflow: hidden;

  &.two_cols, .two_cols & {
    -moz-column-count: 2;
    -moz-column-gap: 40px;
    -webkit-column-count: 2;
    -webkit-column-gap: 40px;
    column-count: 2;
    column-gap: 40px;
  }

  .panel-primary {
    -webkit-column-break-inside:avoid;
    -moz-column-break-inside:avoid;
    -o-column-break-inside:avoid;
    -ms-column-break-inside:avoid;
    column-break-inside:avoid;
    -webkit-break-inside:avoid;
    -moz-break-inside:avoid;
    break-inside:avoid;
    page-break-inside: avoid;

    .panel-heading a {
      &:link, &:visited {
        color: white;
      }

      display: block;
      max-height: 37px;
      overflow: hidden;

      span {
        display: block;
        max-height: 37px;
        position: relative;
        margin-right: -100px;

        &:before {
          content: ' ';
          float: right;
          width: 100px;
          height: 10px;
        }
        &:after {
          content: ' ';
          position: absolute;
          top: 21px;
          right: 84px;
          height: 21px;
          width: 60px;

          /* IE9 SVG, needs conditional override of 'filter' to 'none' */
          background: url(data:image/svg+xml;base64,);
          background: -moz-linear-gradient(left, rgba(66, 139, 202, 0) 0%, rgba(66, 139, 202, 1) 72%, rgba(66, 139, 202, 1) 100%); /* FF3.6+ */
          background: -webkit-gradient(linear, left top, right top, color-stop(0%, rgba(66, 139, 202, 0)), color-stop(72%, rgba(66, 139, 202, 1)), color-stop(100%, rgba(66, 139, 202, 1))); /* Chrome,Safari4+ */
          background: -webkit-linear-gradient(left, rgba(66, 139, 202, 0) 0%, rgba(66, 139, 202, 1) 72%, rgba(66, 139, 202, 1) 100%); /* Chrome10+,Safari5.1+ */
          background: -o-linear-gradient(left, rgba(66, 139, 202, 0) 0%, rgba(66, 139, 202, 1) 72%, rgba(66, 139, 202, 1) 100%); /* Opera 11.10+ */
          background: -ms-linear-gradient(left, rgba(66, 139, 202, 0) 0%, rgba(66, 139, 202, 1) 72%, rgba(66, 139, 202, 1) 100%); /* IE10+ */
          background: linear-gradient(to right, rgba(66, 139, 202, 0) 0%, rgba(66, 139, 202, 1) 72%, rgba(66, 139, 202, 1) 100%); /* W3C */
          filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00428bca', endColorstr='#428bca', GradientType=1); /* IE6-8 */
        }
      }
    }
  }

  .add_meta {
    float: right;
    color: gray;
    font-size: 12px;
  }

  ul.dokumente {
    font-size: 12px;
  }
}

ul.antragsliste {
  list-style-type: none;
  margin: 0;
  padding: 0;
  clear: both;

  .nur_dokumente & {
    -moz-column-count: 2;
    -moz-column-gap: 40px;
    -webkit-column-count: 2;
    -webkit-column-gap: 40px;
    column-count: 2;
    column-gap: 40px;
  }
  .loading & {
    opacity: 0.5;
  }

  li.listitem {
    margin: 0;
    margin-top: 6px;
    border: solid 1px #ccc;
    border-radius: 5px;
    background-color: #eee;
    padding: 1px;
    break-inside: avoid-column;
    display: inline-block;
    width: 100%;


    .add_meta {
      float: right;
      text-align: right;
      font-size: 11px;
      color: gray;
      margin-top: 3px;
      font-weight: bold;

      .partei, .ba {
        border-bottom: dotted 1px gray;
        font-weight: normal;
      }
    }

    .antraglink {
      position: relative;
      overflow: hidden;
      a {
        padding: 1px;
        font-weight: bold;
        display: block;
        max-height: 42px;
        overflow: hidden;
        position: relative;
        margin-right: -100px;

        &:before {
          content: ' ';
          float: right;
          width: 100px;
          height: 10px;
        }
        &:after {
          content: ' ';
          position: absolute;
          top: 21px;
          right: 99px;
          height: 21px;
          width: 50px;

          /* IE9 SVG, needs conditional override of 'filter' to 'none' */
          background: url(data:image/svg+xml;base64,);
          background: -moz-linear-gradient(left, rgba(66, 139, 202, 0) 0%, rgba(66, 139, 202, 1) 72%, rgba(66, 139, 202, 1) 100%); /* FF3.6+ */
          background: -webkit-gradient(linear, left top, right top, color-stop(0%, rgba(66, 139, 202, 0)), color-stop(72%, rgba(66, 139, 202, 1)), color-stop(100%, rgba(66, 139, 202, 1))); /* Chrome,Safari4+ */
          background: -webkit-linear-gradient(left, rgba(66, 139, 202, 0) 0%, rgba(66, 139, 202, 1) 72%, rgba(66, 139, 202, 1) 100%); /* Chrome10+,Safari5.1+ */
          background: -o-linear-gradient(left, rgba(66, 139, 202, 0) 0%, rgba(66, 139, 202, 1) 72%, rgba(66, 139, 202, 1) 100%); /* Opera 11.10+ */
          background: -ms-linear-gradient(left, rgba(66, 139, 202, 0) 0%, rgba(66, 139, 202, 1) 72%, rgba(66, 139, 202, 1) 100%); /* IE10+ */
          background: linear-gradient(to right, rgba(66, 139, 202, 0) 0%, rgba(66, 139, 202, 1) 72%, rgba(66, 139, 202, 1) 100%); /* W3C */
          filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00eeeeee', endColorstr='#eeeeee', GradientType=1); /* IE6-8 */
        }
      }
    }

    div.dokumente {
      margin: 0;
      padding: 3px;
      background-color: #fff;
      font-size: 11px;
    }

    .gefunden_ueber {
      padding-left: 6px;
      background-color: #fff;
      font-size: 11px;
      color: gray;
    }
  }
}

ul.dokumente {
  list-style-type: none;
  margin: 0;
  padding: 3px;
  background-color: #fff;
  font-size: 11px;

  li {
    display: inline;
    margin: 1px;
    padding: 1px;
    font-weight: bold;
    &:before {
      content: '| ';
      color: #428BCA;
    }
    &:first-child:before {
      content: '';
      font-weight: normal;
    }

    a.pdf:before {
      //content: 'pdf'
    }
  }
}
